<template>

  <Modal v-model="showCreate" title="" :mask-closable="false" :closable="false">

    <!--<p slot="close"><Button type="ghost" @click="close" style="margin-left: 8px"></Button></p>-->
    <Form ref="formAlarmInfo" :model="formAlarmInfo" :label-width="80" inline>


      <FormItem label="" prop="variable_id">

        <Select v-model="group_id" style="width:100px" placeholder="" filterable @on-change="post_variable">
          <Option v-for="group in groups" :value="group.id" :key="group.id">{{ group.group_name }}</Option>
        </Select>

        <Select transfer v-model="formAlarmInfo.variable_id" style="width:100px" placeholder=""
                filterable>
          <Option v-for="variable in variables" :value="variable.id" :key="variable.id">{{ variable.variable_name }}
          </Option>
        </Select>

      </FormItem>

      <FormItem label="" prop="alarm_type">
        <Select transfer v-model="formAlarmInfo.alarm_type" style="width:100px" placeholder="" filterable>
          <Option value="1"></Option>
          <Option value="2"></Option>
          <Option value="3"></Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="note">
        <Input v-model="formAlarmInfo.note" placeholder=""></Input>
      </FormItem>


    </Form>
    <div slot="footer">
      <Button type="success" :loading="loading" @click="createSubmit"></Button>
      <Button type="success" :loading="loading" @click="createSubmitContinue"></Button>
      <Button type="ghost" @click="handleClean('formAlarmInfo')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>


</template>


<script>
  import AlarmInfo from '../models/actions/alarm_info'
  import Variable from '../models/actions/variable'

  export default {
    data () {
      return {
        loading: false,
        group_id: '',
        variables: [],
        formAlarmInfo: {
          variable_id: null,
          alarm_type: null,
          note: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showCreate',
      'groups'
    ],
    methods: {
      post_variable () {
        new Variable()
          .POST({
            data: {
              group_id: this.group_id
            }
          })
          .then((res) => {
            this.variables = res.data['data']
          })
      },
      createSubmit () {
        this.loading = true
        new AlarmInfo()
          .PUT({
            data: this.formAlarmInfo
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
            this.$emit('close')
          })
      },
      createSubmitContinue () {
        this.loading = true
        new AlarmInfo()
          .PUT({
            data: this.formAlarmInfo
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
          })
      },
      handleClean (name) {
        this.$refs[name].resetFields()
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



