<template>
  <Card class="login">
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
      <FormItem label="" prop="username">
        <Input v-model="formValidate.username" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="email">
        <Input v-model="formValidate.email" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="password">
        <Input v-model="formValidate.password" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="confirm">
        <Input v-model="formValidate.confirm" placeholder=""></Input>
      </FormItem>


      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')"></Button>
      </FormItem>
    </Form>
  </Card>
</template>
<script>
//  import auth from '../utils/auth'
  import Model from '../models/actions/register'

  export default {
    data () {
      return {
        formValidate: {
          username: '',
          email: '',
          password: '',
          confirm: ''
        },
        ruleValidate: {
          username: [
            {required: true, message: '', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur'}
          ],
          password: [
            {required: true, type: 'string', min: 6, message: '6', trigger: 'blur'},
            {type: 'string', max: 32, message: '32', trigger: 'blur'}
          ],
          confirm: [
            {required: true, type: 'string', min: 6, message: '6', trigger: 'blur'},
            {type: 'string', max: 32, message: '32', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            new Model()
              .PUT({
                data: this.formValidate
              })
              .then((res) => {
                this.$Message.success(res.data['msg'])
//                this.$Message.success('!')
//                auth.login(res.data.data)
                this.$route.push('/')
              })
          } else {
            this.$Message.error('!')
          }
        })
      }
    }
  }
</script>



