<template>

  <Modal v-model="showCreate" title="" :mask-closable="false" :closable="false">

    <!--<p slot="close"><Button type="ghost" @click="close" style="margin-left: 8px"></Button></p>-->
    <Form ref="formStation" :model="formStation" :label-width="80" inline>

      <FormItem label="" prop="station_name">
        <Input v-model="formStation.station_name" placeholder=""></Input>
      </FormItem>

      <FormItem label="MAC" prop="mac">
        <Input v-model="formStation.mac" placeholder="MAC"></Input>
      </FormItem>

      <FormItem label="IP" prop="ip">
        <Input v-model="formStation.ip" placeholder="IP"></Input>
      </FormItem>

      <FormItem label="PLC" prop="plc_count">
        <Input v-model="formStation.plc_count" placeholder="PLC"></Input>
      </FormItem>

      <FormItem label="" prop="id_num">
        <Input v-model="formStation.id_num" placeholder=""></Input>
      </FormItem>

      <FormItem label="" prop="note">
        <Input v-model="formStation.note" placeholder=""></Input>
      </FormItem>

    </Form>

    <div slot="footer">
      <Button type="success" :loading="loading" @click="createSubmit"></Button>
      <Button type="success" :loading="loading" @click="createSubmitContinue"></Button>
      <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>

</template>

<script>
  import Station from '../models/actions/station'

  export default {
    data () {
      return {
        loading: false,
        formStation: {
          station_name: null,
          mac: null,
          ip: null,
          id_num: null,
          plc_count: null,
          note: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showCreate'
    ],
    methods: {
      createSubmit () {
        this.loading = true
        new Station()
          .PUT({
            data: this.formStation
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
            this.$emit('close')
          })
      },
      createSubmitContinue () {
        this.loading = true
        new Station()
          .PUT({
            data: {}
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
          })
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



