<template>

  <Modal v-model="showEdit" title="" :mask-closable="false" :closable="false">

    <div slot="header">
      <span style="float: left"></span>
      <span style="float: right">
      <Button type="ghost" size="small" @click="close" style="margin-left: 8px">
        <Icon type="close"></Icon>
      </Button>
        </span>
      <br><br>
    </div>

    <Form ref="formStation" :model="formStation" :label-width="80" inline>

      <FormItem label="" prop="station_name">
        <Input v-model="formStation.station_name" placeholder=""></Input>
      </FormItem>

      <FormItem label="MAC" prop="mac">
        <Input v-model="formStation.mac" placeholder="MAC"></Input>
      </FormItem>

      <FormItem label="IP" prop="ip">
        <Input v-model="formStation.ip" placeholder="IP"></Input>
      </FormItem>

      <FormItem label="PLC" prop="plc_count">
        <Input v-model="formStation.plc_count" placeholder="PLC"></Input>
      </FormItem>

      <FormItem label="" prop="id_num">
        <Input v-model="formStation.id_num" placeholder=""></Input>
      </FormItem>

      <FormItem label="" prop="note">
        <Input v-model="formStation.note" placeholder=""></Input>
      </FormItem>

    </Form>

    <div slot="footer">
      <Button type="success" :loading="loading" @click="editSubmit('formStation')"></Button>
      <Button type="ghost" @click="handleReset()" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="handleClean('formStation')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>


</template>


<script>
  import Station from '../models/actions/station'

  export default {
    data () {
      return {
        loading: false,
        formStation: {
          id: null,
          station_name: null,
          mac: null,
          ip: null,
          id_num: null,
          plc_count: null,
          note: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showEdit',
      'data'
    ],
    watch: {
      data: function () {
        this.data2form()
      }
    },
    methods: {
      data2form () {
        this.formStation.id = this.data.id
        this.formStation.station_name = this.data.station_name
        this.formStation.mac = this.data.mac
        this.formStation.ip = this.data.ip
        this.formStation.id_num = this.data.id_num
        this.formStation.plc_count = this.data.plc_count
        this.formStation.note = this.data.note
      },
      editSubmit (name) {
        this.loading = true
        this.$refs[name].validate((valid) => {
          if (valid) {
            new Station()
              .PUT({
                data: this.formStation
              })
              .then((res) => {
                this.loading = false
                if (res.data['ok'] === 1) {
                  this.$Message.success(res.data['msg'])
                } else {
                  this.$Message.error(res.data['msg'])
                }
                this.$emit('close')
              })
          } else {
            this.$Message.error('!')
          }
        })
      },
      handleReset () {
        this.data2form()
        this.$Message.warning('')
      },
      handleClean (name) {
        this.$refs[name].resetFields()
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



