<template>

  <Modal v-model="showCreate" title="" :mask-closable="false" :closable="false">

    <!--<p slot="close"><Button type="ghost" @click="close" style="margin-left: 8px"></Button></p>-->
    <Form ref="formPLC" :model="formPLC" :label-width="80" inline>

      <FormItem label="PLC" prop="plc_name">
        <Input v-model="formPLC.plc_name" placeholder="PLC"></Input>
      </FormItem>

      <FormItem label="" prop="station_id">
        <Select transfer v-model="formPLC.station_id" style="width:100px" placeholder=""
                filterable>
          <Option v-for="station in stations" :value="station.id" :key="station.id">{{ station.station_name }}</Option>
        </Select>
      </FormItem>

       <FormItem label="IP" prop="ip">
        <Input v-model="formPLC.ip" placeholder="IP"></Input>
      </FormItem>

      <FormItem label="MPI" prop="mpi">
        <Input v-model="formPLC.mpi" placeholder="MPI"></Input>
      </FormItem>

      <FormItem label="" prop="type">
        <Select transfer v-model="formPLC.type" style="width:100px" placeholder="" filterable>
          <Option value="1"></Option>
          <Option value="2">MPI</Option>
          <Option value="3">S7ONLIN</Option>
        </Select>
      </FormItem>

      <FormItem label="PLC" prop="plc_type">
        <Select transfer v-model="formPLC.plc_type" style="width:100px" placeholder=""
                filterable>
          <Option value="S7-200 Smart">S7-200 Smart</Option>
          <Option value="S7-200">S7-200</Option>
          <Option value="S7-300PN/DP">S7-300PN/DP</Option>
          <Option value="S7-300 DP">S7-300 DP</Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="note">
        <Input v-model="formPLC.note" placeholder=""></Input>
      </FormItem>

    </Form>
    <div slot="footer">
      <Button type="success" :loading="loading" @click="createSubmit"></Button>
      <Button type="success" :loading="loading" @click="createSubmitContinue"></Button>
      <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>
</template>

<script>
  import PLC from '../models/actions/plc'

  export default {
    data () {
      return {
        loading: false,
        formPLC: {
          plc_name: null,
          station_id: null,
          ip: null,
          mpi: null,
          type: null,
          plc_type: null,
          note: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showCreate',
      'stations'
    ],
    methods: {
      createSubmit () {
        this.loading = true
        new PLC()
          .PUT({
            data: this.formPLC
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
            this.$emit('close')
          })
      },
      createSubmitContinue () {
        this.loading = true
        new PLC()
          .PUT({
            data: this.formPLC
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
          })
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



