//  host
const HOST = window.location.host

// 
const DEV = 'DEV'

// 
const PROD = 'PROD'

// 
const ENV = HOST === 'localhost:8080' ? DEV : PROD

// 
const BASE_URL = ENV === DEV ? 'http://localhost:8000' : 'http://1.plcweb.applinzi.com'

// 
const API_URL = BASE_URL + '/api'

// 
const PAGE_SIZE = 10

// 
const TITLE = ''

export default {
  BASE_URL,
  API_URL,
  PAGE_SIZE,
  TITLE
}



