<template>
  <Modal v-model="showCreate" title="" :mask-closable="false" :closable="false">

    <!--<p slot="close"><Button type="ghost" @click="close" style="margin-left: 8px"></Button></p>-->
    <Form ref="formVariable" :model="formVariable" :label-width="80" inline>

      <FormItem label="" prop="variable_name">
        <Input v-model="formVariable.variable_name" placeholder=""></Input>
      </FormItem>

      <FormItem label="" prop="group_id">
        <Select transfer v-model="formVariable.group_id" style="width:100px" placeholder=""
                filterable>
          <Option v-for="group in groups" :value="group.id" :key="group.id">{{ group.group_name }}</Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="area">
        <Select transfer v-model="formVariable.area" style="width:100px" placeholder="" filterable>
          <Option value="1">DB</Option>
          <Option value="2">Input</Option>
          <Option value="3">Output</Option>
          <Option value="4">Merkers</Option>
        </Select>
      </FormItem>

      <FormItem label="DB" prop="db_num">
        <Input v-model="formVariable.db_num" placeholder="DB"></Input>
      </FormItem>

      <FormItem label="" prop="address">
        <Input v-model="formVariable.address" placeholder=""></Input>
      </FormItem>

      <FormItem label="" prop="data_type">
        <Select transfer v-model="formVariable.data_type" style="width:100px" placeholder=""
                filterable>
          <Option value="FLOAT">FLOAT</Option>
          <Option value="INT">INT</Option>
          <Option value="DINT">DINT</Option>
          <Option value="WORD">WORD</Option>
          <Option value="BYTE">BYTE</Option>
          <Option value="BOOL">BOOL</Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="rw_type">
        <RadioGroup v-model="formVariable.rw_type">
          <Radio label="1"></Radio>
          <Radio label="2"></Radio>
          <Radio label="3"></Radio>
        </RadioGroup>
      </FormItem>

      <FormItem label="">
        <RadioGroup v-model="formVariable.upload">
          <Radio label="1"></Radio>
          <Radio label="0"></Radio>
        </RadioGroup>
      </FormItem>

      <FormItem label="" prop="acquisition_cycle">
        <Select transfer v-model="formVariable.acquisition_cycle" style="width:100px" placeholder=""
                filterable>
          <Option value="1">1s</Option>
          <Option value="10">10s</Option>
          <Option value="30">30s</Option>
          <Option value="60">60s</Option>
          <Option value="600">600s</Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="server_record_cycle">
        <Select transfer v-model="formVariable.server_record_cycle" style="width:100px"
                placeholder=""
                filterable>
          <Option value="10">10s</Option>
          <Option value="30">30s</Option>
          <Option value="60">60s</Option>
          <Option value="600">600s</Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="write_value">
        <Input v-model="formVariable.write_value" placeholder=""></Input>
      </FormItem>

      <FormItem label="" prop="note">
        <Input v-model="formVariable.note" placeholder=""></Input>
      </FormItem>

    </Form>
    <div slot="footer">
      <Button type="success" :loading="loading" @click="createSubmit"></Button>
      <Button type="success" :loading="loading" @click="createSubmitContinue"></Button>
      <Button type="ghost" @click="handleClean('formVariable')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>

</template>

<script>
  import Group from '../models/actions/group'
  import Variable from '../models/actions/variable'

  export default {
    data () {
      return {
        loading: false,
        formVariable: {
          variable_name: null,
          group_id: null,
          address: null,
          acquisition_cycle: null,
          data_type: null,
          db_num: null,
          note: null,
          rw_type: null,
          server_record_cycle: null,
          ten_id: null,
          upload: null,
          write_value: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showCreate',
      'groups'
    ],
    methods: {
      get_group () {
        new Group()
          .GET()
          .then((res) => {
            this.groups = res.data['data']
          })
      },
      post_variable () {
        new Variable()
          .POST({
            data: {
              group_id: this.group_id
            }
          })
          .then((res) => {
            this.variables = res.data['data']
          })
      },
      createSubmit () {
        this.loading = true
        new Variable()
          .PUT({
            data: this.formVariable
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
            this.handleClean('formVariable')
            this.$emit('close')
          })
      },
      createSubmitContinue () {
        this.loading = true
        new Variable()
          .PUT({
            data: this.formVariable
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
          })
      },
      handleClean (name) {
        this.$refs[name].resetFields()
      },
      close () {
        this.handleClean('formVariable')
        this.$emit('close')
      }
    }
  }
</script>



