// import iView from 'iview'
import auth from '../auth'

/**
 * REST 
 * @type {Object}
 */
export default {
  /**
   * 
   * @param {Object} res 
   */
  successHandler (res) {},

  /**
   * 
   * @param {Object} res 
   */
  errorHandler (res) {
    console.log(res.response)
    // iView.Message.error(res.response.data.error.message)
  },

  /**
   *  Headers
   * @return {Object}
   */
  getHeaders () {
    const {manager, token} = auth.get()

    return {
      auth: window.btoa(`${manager.username}\n${token}`)
    }
  }
}



