<template>
  <Modal v-model="showEdit" title="" :mask-closable="false" :closable="false">

    <div slot="header">
      <span style="float: left"></span>
      <span style="float: right">
      <Button type="ghost" size="small" @click="close" style="margin-left: 8px" inline>
        <Icon type="close"></Icon>
      </Button>
        </span>
      <br><br>
    </div>

    <Form ref="formGroup" :model="formGroup" :label-width="80">

      <FormItem label="" prop="group_name">
        <Input v-model="formGroup.group_name" placeholder=""></Input>
      </FormItem>

      <FormItem label="PLC" prop="plc_id">
        <Select transfer v-model="formGroup.plc_id" style="width:100px" placeholder="PLC"
                filterable>
          <Option v-for="plc in plcs" :value="plc.id" :key="plc.id">{{ plc.plc_name }}</Option>
        </Select>
      </FormItem>


      <FormItem label="" prop="upload_cycle">
        <Select transfer v-model="formGroup.upload_cycle" style="width:100px" placeholder=""
                filterable>
          <Option value="60"></Option>
          <Option value="300"></Option>
          <Option value="600"></Option>
          <Option value="900"></Option>
          <Option value="1800"></Option>
          <Option value="3600"></Option>
        </Select>
      </FormItem>

      <FormItem label="" prop="note">
        <Input v-model="formGroup.note" placeholder=""></Input>
      </FormItem>

    </Form>

    <div slot="footer">
      <Button type="success" :loading="loading" @click="editSubmit('formGroup')"></Button>
      <Button type="ghost" @click="handleReset()" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="handleClean('formGroup')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>
</template>

<script>
  import Group from '../models/actions/group'

  export default {
    data () {
      return {
        loading: false,
        formGroup: {
          id: null,
          group_name: null,
          plc_id: null,
          upload_cycle: null,
          note: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showEdit',
      'plcs',
      'data'
    ],
    watch: {
      data: function () {
        this.data2form()
      }
    },
    methods: {
      data2form () {
        this.formGroup.id = this.data.id
        this.formGroup.group_name = this.data.group_name
        this.formGroup.plc_id = this.data.plc_id
        if (this.data.upload_cycle) {
          this.formGroup.upload_cycle = this.data.upload_cycle.toString()
        }
      },
      editSubmit (name) {
        this.loading = true
        this.$refs[name].validate((valid) => {
          if (valid) {
            new Group()
              .PUT({
                data: this.formGroup
              })
              .then((res) => {
                this.loading = false
                if (res.data['ok'] === 1) {
                  this.$Message.success(res.data['msg'])
                } else {
                  this.$Message.error(res.data['msg'])
                }
                this.$emit('close')
              })
          } else {
            this.$Message.error('!')
          }
        })
      },
      handleReset () {
        this.data2form()
        this.$Message.warning('')
      },
      handleClean (name) {
        this.$refs[name].resetFields()
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



