<template>

  <Modal v-model="showCreate" title="" :mask-closable="false" :closable="false">

    <!--<p slot="close"><Button type="ghost" @click="close" style="margin-left: 8px"></Button></p>-->

    <Form ref="formParam" :model="formParam" :label-width="80" inline>

      <FormItem label="" prop="param_name">
        <Input v-model="formParam.param_name" placeholder=""></Input>
      </FormItem>

      <FormItem label="" prop="unit">
        <Input v-model="formParam.unit" placeholder=""></Input>
      </FormItem>


    </Form>

    <div slot="footer">
      <Button type="success" :loading="loading" @click="createSubmit"></Button>
      <Button type="success" :loading="loading" @click="createSubmitContinue"></Button>
      <Button type="ghost" @click="handleClean('formParam')" style="margin-left: 8px"></Button>
      <Button type="ghost" @click="close" style="margin-left: 8px"></Button>
    </div>
  </Modal>

</template>

<script>
  import Variable from '../models/actions/variable'

  import Param from '../models/actions/param'

  export default {
    data () {
      return {
        loading: false,
        formParam: {
          param_name: null,
          unit: null,
          variable_id: null
        }
//        ruleVariableCreate: {
//          variable_name: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          mail: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'email', message: '', trigger: 'blur'}
//          ],
//          city: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          gender: [
//            {required: true, message: '', trigger: 'change'}
//          ],
//          interest: [
//            {required: true, type: 'array', min: 1, message: '', trigger: 'change'},
//            {type: 'array', max: 2, message: '', trigger: 'change'}
//          ],
//          date: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          time: [
//            {required: true, type: 'date', message: '', trigger: 'change'}
//          ],
//          desc: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 20, message: '20', trigger: 'blur'}
//          ]
//        }
      }
    },
    props: [
      'showCreate',
      'variable_id'
    ],
    watch: {
      variable_id: function () {
        this.formParam.variable_id = this.variable_id
      }
    },
    methods: {
      post_variable () {
        new Variable()
          .POST({
            data: {
              group_id: this.group_id
            }
          })
          .then((res) => {
            this.variables = res.data['data']
          })
      },
      createSubmit () {
        this.loading = true
        new Param()
          .PUT({
            data: this.formParam
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
            this.$emit('close')
          })
      },
      createSubmitContinue () {
        this.loading = true
        new Param()
          .PUT({
            data: this.formParam
          })
          .then((res) => {
            this.loading = false
            if (res.data['ok'] === 1) {
              this.$Message.success(res.data['msg'])
            } else {
              this.$Message.error(res.data['msg'])
            }
          })
      },
      handleClean (name) {
        this.$refs[name].resetFields()
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



